import React from 'react';
import './Users.css';

const Users = ( {user}  ) => {
    
    return (
        <div>
            <div className="bodies">
                 <h1>{user.FirstName} {""} {user.LastName}</h1>
                 <p><b>Username:</b> <span className ="value">{user.UserName}</span></p>
                 <p><b>Gender:</b> <span className ="value">{user.Gender}</span></p>
                 <p><b>Email:</b> <span className ="value">{user.Email}</span></p>
                 <p><b>Domain name:</b> <span className ="value">{user.DomainName}</span></p>
                 <p><b>URL:</b> <span className ="value">{user.URL}</span></p>
                 <p><b>Mac Address:</b> <span className ="value">{user.MacAddress}</span></p>
                 <p><b>Last LogIn:</b> <span className ="value">{user.LastLogin}</span></p>
                 <p><b>Latitude:</b> <span className ="value">{user.Latitude}</span></p>
                 <p><b>Longitude:</b> <span className ="value">{user.Longitude}</span></p>
                 <p><b>CreditCardType:</b> <span className ="value">{user.CreditCardType}</span></p>
                 <p><b>CreditCardNumber:</b> <span className ="value">{user.CreditCardNumber}</span></p>
                 <p><b>PaymentMethod:</b> <span className ="value">{user.PaymentMethod}</span></p>
            </div>
            
        </div>
    )
}

export default Users;