import React, { useState, useEffect } from 'react';
import axios from 'axios';
import Posts from './component/Posts';
import Pagination from './component/Pagination';
import ScrollUp from './component/ScrollUp';
import { useCombobox } from 'downshift';
import './App.css';

 const App = () => {
    const [inputItems, setInputItems] = useState([])
    const [posts, setPosts] = useState([]);
    const [loading, setLoading] = useState(false);
    const [currentPage, setCurrentPage] = useState(1);
    const [postPerPage] = useState(20)

    useEffect (() => {
        const fetchPosts = async () => {
            setLoading(true);
            const res =  await axios.get('https://api.enye.tech/v1/challenge/records');

            setPosts(res.data.records.profiles);   
           
            setLoading(false);
        };

        fetchPosts();
    },[]);

    const {isOpen, getMenuProps, getInputProps,getComboboxProps, getItemProps} = useCombobox({
        items:inputItems,
        onInputValueChange:({inputValue}) => {
            setInputItems(
                posts.filter((user) =>
                 user.FirstName.toLowerCase().startsWith(inputValue.toLowerCase())
                )
            )
        }
    })


    //get current posts
    const Lastindexofpage = currentPage * postPerPage;
    const Firstindexofpage = Lastindexofpage - postPerPage;
    const currentpost = posts.slice(Firstindexofpage, Lastindexofpage);

    //pagination functionality

    const paginate = (pageNumber) => setCurrentPage(pageNumber);

    //filtering button functionality

    const handleBtns = (e) => {
        // console.log(e.target.value);
            if(e.target.value==="Male") {
                setPosts(posts.filter(user=>user.Gender===e.target.value))
            }
            else if(e.target.value==="Female") {
                setPosts(posts.filter(user=>user.Gender===e.target.value))
            }
            else if(e.target.value==="cc") {
                setPosts(posts.filter(user=>user.PaymentMethod===e.target.value))
            }
            else if(e.target.value==="check") {
                setPosts(posts.filter(user=>user.PaymentMethod===e.target.value))
            }
            else if(e.target.value==="money order") {
                setPosts(posts.filter(user=>user.PaymentMethod===e.target.value))
            }
            else if(e.target.value==="paypal") {
                setPosts(posts.filter(user=>user.PaymentMethod===e.target.value))
            }else {
                setPosts(posts)
            }
    
    }


    //scroll to bottom

    const scrollToBottom = () =>window.scrollTo({
        top: document.documentElement.scrollHeight,
        behavior:"smooth"
    })


    return (
        
        <div>
            <div className="container">
                <h1>SEDAQ <span className="small">SALES</span></h1>
                <p className="tag">Transaction for the year</p>
                <div {...getComboboxProps()}>
                    <input {...getInputProps()} 
                     
                      placeholder="seach for a patient"
                     
                    />
                </div>
                <ul {...getMenuProps()} >
                    {isOpen &&
                        inputItems.map((item, index) =>(
                            <span
                                key={item}
                                {...getItemProps({item, index})}
                                onClick={()=>setPosts(item.FirstName)}>
                                <li>
                                    {item.FirstName}
                                </li>
                            </span>
                        ))
                    }
                </ul>
                <div className="scroll-to-bottom " onClick= {scrollToBottom}>
                     <i class="icon fas fa-angle-down"></i>
                </div>
                
                <Posts posts={currentpost} loading={loading}  handleBtns= {handleBtns} />
                <Pagination postPerPage={postPerPage} totalPosts={posts.length} paginate={paginate}/>
                <ScrollUp />
                <p className="copyright">CopyrightÂ© by yusuf oluwatosin</p>
            </div>
            
        </div>
    )
};
export default App;
