import React from 'react';
import Users from './Users'

const Posts = ({ posts , loading, handleBtns }) => {
    if (loading) {
        return <h2>loading...</h2>;
    }

    return (
        <React.Fragment>
                
                <div className="dropdown">
                    <button className="dropbtn">Search by Gender</button>
                        <div className="dropdown-content">
                            <button className="drop" onClick={handleBtns} value="Male">Male</button>
                            <button className="drop" onClick={handleBtns} value="Female">Female</button>
                            
                        </div>
                </div>
                <div className="dropdown1">
                    <button className="dropbtn">Search by Payment Method</button>
                        <div className="dropdown-content">
                            <button className="drop" onClick={handleBtns} value="check">check</button>
                            <button className="drop" onClick={handleBtns} value="money orde">money orde</button>
                            <button className="drop" onClick={handleBtns} value="paypal">paypal</button>
                            <button className="drop" onClick={handleBtns} value="cc">cc</button>
                        </div>
                </div>
                <div>
                    {posts.map( (i, key) =>(
                        < Users key={key} user={i} />
                    ))}
                </div>
        </React.Fragment>
        
        
    );
};

export default Posts;
